import React, { useState, useEffect } from 'react'
import * as S from './style'
import Brand from '../../icons/brand.svg'
import Center from '../grid/center'
import Nav from './nav'

const Header = () => {
  const [translucent, setTranslucent] = useState(true)

  useEffect(() => {
    window.onscroll = () =>
      window.scrollY < 5 ? setTranslucent(true) : setTranslucent(false)
  }, [])

  return (
    <S.Header className={translucent && 'translucent'}>
      <Center>
        <S.Display>
          <S.Logo as="img" src={Brand} alt="PipFit" />
          <Nav translucent={translucent} />
        </S.Display>
      </Center>
    </S.Header>
  )
}

export default Header
